<template>
  <v-container>
    <Toolbar></Toolbar>
    <v-sheet elevation="1">
      <v-row>
        <v-col cols="12" sm="6">
          <div class="pl-3">
            <v-img
              :src="item.image"
              :alt="item.name"
              lazy-src="data:image/png;base64,"
            />
          </div>
        </v-col>
        <v-col cols="12" sm="5">
          <div class="pa-4">
            <div class="text-overline">{{ item.taste }}</div>
            <h1 class="text-h2 mb-4" :style="`color:${item.color}!important`">
              {{ item.name }}
            </h1>
            <p>{{ item.description }}</p>
            <p>
              <strong>Expires:</strong> {{ item.expiresDate }} @
              {{ item.expiresTime }}
            </p>
            <p>
              <v-chip :ripple="false" small color="primary" class="mt-2"
                >${{ item.price }}</v-chip
              >
            </p>

            <ItemActions
              :id="item.id"
              :deleteCallback="deleteCallback"
            ></ItemActions>
          </div>
        </v-col>
      </v-row>
    </v-sheet>
  </v-container>
</template>

<script>
import DataService from "@/services/DataService";
import ItemActions from "@/components/ItemActions.vue";

export default {
  components: { ItemActions },
  name: "ViewItem",
  data() {
    return {
      item: {},
    };
  },
  methods: {
    deleteCallback() {
      this.$router.push("/");
    },
    getItem(id) {
      DataService.get(id)
        .then((response) => {
          this.item = response.data;
          this.item.expiresDate = this.item.expires.substr(0, 10);
          this.item.expiresTime = this.item.expires.substr(11, 8);
        })
        .catch((e) => {
          console.log(e);
        });
    },
  },
  mounted() {
    this.getItem(this.$route.params.id);
  },
};
</script>

<style lang="scss">
@import "@/scss/main.scss";
</style>
